/*--------------------------------------------------------------
# Editor
--------------------------------------------------------------*/
.components-base-control__flex {
	align-items: center;
	display: flex;
}
.components-base-control--label-block .components-base-control__label {
	display: block;
}

/*--------------------------------------------------------------
## Tabs
--------------------------------------------------------------*/
.edit-post-sidebar .components-tab-panel__tabs {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(40px, 1fr));
	margin-bottom: 1em;
}
.edit-post-sidebar .components-tab-panel__tabs > button {
	align-items: center;
	background-color: transparent;
	border-radius: 0;
	border-style: solid;
	border-width: 0 0 2px 0;
	cursor: pointer;
	display: inline-flex;
	justify-content: center;
	margin: 0;
	outline: 0;
	padding-bottom: 5px;
}
.edit-post-sidebar .components-insp-tabs__active {
	border-color: #1e9ff2;
}
.edit-post-sidebar .components-insp-tabs > .components-tab-panel__tab-content .components-toolbar {
	border: 0;
}
